import React from "react";
import { useNavigate } from "react-router-dom";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector, useDispatch } from "react-redux";
import { Button } from "@material-ui/core";
import { Icon } from "@iconify/react";
import menuIcon from "@iconify/icons-ant-design/menu-outline";

//
import { useStyles } from "./components/navstyles";
import DoneeLogo from "../../assets/icons/doneelogo.svg";
import MenuNavbar from "./components/MenuNavbar";
import MenuPopover from "../../components/MenuPopover";
import {
  CreateProfileResponse,
  RootAppState,
  ILoginResponse,
} from "../../utils/typeHelpers";
// import { GET_DONOR_PROFILE_API } from "../../utils/Api";
import { tokenInitialValue } from "../../utils/initialvalues";
import CreateProfileMenu from "../CreateProfileMenu";
import { saveUserInfoAction } from "../../redux/actions/saveUserInfoAction";
import { saveLoginAction } from "../../redux/actions/loginAction";
import NotificationsPopover from "../../screen/Home/NGO/Dashboard/components/NotificationsPopover";
import ErrorCatchComponent from "../../components/ErrorCatchComponent";
import ApiErrorPage from "../../screen/Errror/ApiErrorPage";
import useAxios from "../../utils/useAxios";

export default function NavbarLoggedIn() {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  // const { data, isSuccess }: UseQueryResult<CreateProfileResponse, Error> =
  //   useQuery(["userInfo", userState.userInfo.access], () =>
  //     GET_DONOR_PROFILE_API(userState.userInfo.access)
  //   );

  const axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
    isSuccess,
  }: UseQueryResult<CreateProfileResponse, Error> = useQuery(
    ["userInfo", userState.userInfo.access],
    async () => {
      const response = await axiosInstance.get(`/user-detail/`);

      return response.data;
    }
  );

  //hooks
  const classes = useStyles();
  let navigate = useNavigate();
  const dispatch = useDispatch();
  //variables
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMobileMenuOpen = (event: any) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  // const openDashboard = () => navigate("../dashboard");
  const mobileMenuId = "mobile-menu-logged-in";

  const handleLogout = () => {
    const userInfo: ILoginResponse = tokenInitialValue;

    dispatch(saveLoginAction(false));
    dispatch(saveUserInfoAction(userInfo));
    navigate("../");
  };
  const renderMobileMenu = (
    <MenuPopover
      id={mobileMenuId}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
      anchorEl={mobileMoreAnchorEl}
      sx={{ width: 220 }}
    >
      <MenuItem
        onClick={() => {
          handleMobileMenuClose();
          navigate("../profile");
        }}
      >
        <p>User</p>
      </MenuItem>
      <MenuItem
        onClick={() => {
          handleMobileMenuClose();
          navigate("../faq");
        }}
      >
        <p>FAQ</p>
      </MenuItem>
      {userState.userInfo.user_profile !== null && (
        <MenuItem
          onClick={() => {
            handleMobileMenuClose();
            navigate("../profiledetail");
          }}
        >
          <p>Profile Detail</p>
        </MenuItem>
      )}
      {userState.userInfo.user_profile?.profile_type === "NGO" && (
        <MenuItem
          onClick={() => {
            handleMobileMenuClose();
            navigate("../dashboard");
          }}
        >
          <p>Dashboard</p>
        </MenuItem>
      )}
      <MenuItem
        onClick={() => {
          handleMobileMenuClose();
          navigate("../creategoal");
        }}
      >
        <p>Create Goal</p>
      </MenuItem>
      <MenuItem onClick={handleLogout}>
        <p>Logout</p>
      </MenuItem>
    </MenuPopover>
  );

  // console.log('==-- comming *-**-==');

  return (
    <ErrorCatchComponent>
      {isLoading === false ? (
        <div>
          {isError ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
            <div className={`${classes.grow}`}>
              <AppBar position="static" className={classes.appbarColor}>
                <Toolbar className="navbarStyleCustomize">
                  <img
                    src={DoneeLogo}
                    alt="logo"
                    className="navIcons"
                    style={{ cursor: "pointer" }}
                    onClick={() => navigate("../")}
                  />

                  <div className={classes.grow} />
                  <div className={classes.sectionDesktop}>
                    {data !== undefined && isSuccess ? (
                      <div>
                        {data.user_profile === null ? (
                          <CreateProfileMenu />
                        ) : (
                          <>
                            <Button
                              onClick={() => navigate("../invitedonee")}
                              color="inherit"
                            >
                              Invite Donee
                            </Button>
                            <Button
                              onClick={() => navigate("../faq")}
                              color="inherit"
                            >
                              FAQ
                            </Button>
                            {data.user_profile.profile_type === "NGO" && (
                              <Button
                                onClick={() => navigate("../dashboard")}
                                color="inherit"
                              >
                                Dashboard
                              </Button>
                            )}
                          </>
                        )}

                        <MenuNavbar avatarProfile={data?.image} />
                        <NotificationsPopover iconColor="white" />
                      </div>
                    ) : (
                      <div />
                    )}
                  </div>
                  <div className={classes.sectionMobile}>
                    <NotificationsPopover iconColor="white" />
                    <IconButton
                      aria-label="show more"
                      aria-controls={mobileMenuId}
                      aria-haspopup="true"
                      onClick={handleMobileMenuOpen}
                      color="inherit"
                    >
                      <Icon icon={menuIcon} />
                    </IconButton>
                  </div>
                </Toolbar>
              </AppBar>
              {renderMobileMenu}
            </div>
          )}
        </div>
      ) : (
        <div />
      )}
    </ErrorCatchComponent>
  );
}
