import React from "react";
import { useNavigate } from "react-router-dom";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import { Icon } from "@iconify/react";
import menuIcon from "@iconify/icons-ant-design/menu-outline";
import { Button } from "@material-ui/core";

//
import { useStyles } from "./components/navstyles";
import DoneeLogo from "../../assets/icons/doneelogo.svg";
import MenuPopover from "../../components/MenuPopover";

export default function NavBarNotLoggedIn() {
  //hooks
  const classes = useStyles();
  let navigate = useNavigate();

  //variables
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMobileMenuOpen = (event: any) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const mobileMenuId = "mobile-menu";
  const renderMobileMenu = (
    <MenuPopover
      id={mobileMenuId}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
      anchorEl={mobileMoreAnchorEl}
      sx={{ width: 220 }}
    >
      <MenuItem onClick={() => navigate("../login")}>
        <p>Login</p>
      </MenuItem>
      <MenuItem onClick={() => navigate("../faq")}>
        <p>FAQ</p>
      </MenuItem>
    </MenuPopover>
  );

  return (
    <div className={`${classes.grow}`}>
      <AppBar position="static" className={classes.appbarColor}>
        <Toolbar className="navbarStyleCustomize">
          <img
            style={{ cursor: "pointer" }}
            src={DoneeLogo}
            alt="logo"
            className="navIcons"
            onClick={() => navigate("../")}
          />
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <Button color="inherit" onClick={() => navigate("../login")}>
              Login
            </Button>
          </div>
          <div className={classes.sectionDesktop}>
            <Button color="inherit" onClick={() => navigate("../faq")}>
              FAQ
            </Button>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <Icon icon={menuIcon} />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
    </div>
  );
}
