import { useRef, useState, useEffect } from "react";

import { useNavigate } from "react-router";

// material
import { alpha } from "@material-ui/core/styles";
import {
  List,
  Avatar,
  IconButton,
  Typography,
  ListItemText,
  ListItemAvatar,
  ListItemButton,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";

import userIcon from "../../../assets/iconmenu/profile.png";
import profileDetailIcon from "../../../assets/iconmenu/profile_detail.png";
import goalsIcon from "../../../assets/iconmenu/donation.png";
import logoutIcon from "../../../assets/iconmenu/logout.png";

import MenuPopover from "../../../components/MenuPopover";
import Scrollbar from "../../../components/Scrollbar";

import { ILoginResponse, RootAppState } from "../../../utils/typeHelpers";

import { saveLoginAction } from "../../../redux/actions/loginAction";
import { tokenInitialValue } from "../../../utils/initialvalues";
import { saveUserInfoAction } from "../../../redux/actions/saveUserInfoAction";
import { saveNgoProfileIDaction } from "../../../redux/actions/saveNgoProfileIDaction";
// utils

// components

// ----------------------------------------------------------------------
interface DataType {
  label: string;
  icon: string;
  navigateRoute: string;
  handleClose?: () => void;
}

function RenderItem({ label, icon, navigateRoute, handleClose }: DataType) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleOnPress = () => {
    const userInfo: ILoginResponse = tokenInitialValue;
    if (handleClose !== undefined) {
      if (label === "Logout") {
        handleClose();
        dispatch(saveLoginAction(false));
        dispatch(saveUserInfoAction(userInfo));
        dispatch(saveNgoProfileIDaction(""));
        navigate(navigateRoute);
      } else {
        handleClose();
        navigate(navigateRoute);
        <p>Logout</p>
      }
    }
  };
  return (
    <ListItemButton
      disableGutters
      sx={{
        py: 1,
        px: 4,
        mt: "1px",
      }}
      onClick={handleOnPress}
    >
      <ListItemAvatar>
        <img
          style={{ height: 15, width: 15, objectFit: "contain" }}
          src={icon}
        />
      </ListItemAvatar>
      <ListItemText
        primary={
          <Typography
            variant="caption"
            sx={{
              display: "flex",
              alignItems: "center",
              color: "text.disabled",
            }}
          >
            {label}
          </Typography>
        }
      />
    </ListItemButton>
  );
}

export default function MenuNavbar({
  avatarProfile,
}: {
  avatarProfile?: string;
}) {
  const anchorRef = useRef(null);
  const state = useSelector((state: RootAppState) => state.AuthReducer);

  const [open, setOpen] = useState(false);
  // console.log("state.userInfo.user_profile: ", state.userInfo.user_profile?.profile_type);
  const dataProfileCreated: DataType[] = [
    {
      label: state.userInfo.user_profile?.profile_type === "NGO" ? "NGO Profile" : "Donee Profile",
      icon: userIcon,
      navigateRoute: "../profiledetail",
    },
    {
      label: "Create Goal",
      icon: goalsIcon,
      navigateRoute: "../creategoal",
    },
  ];

  const dataNotLoggedIn: DataType[] = [
    {
      label: "Create Donor Profile",
      icon: userIcon,
      navigateRoute: "../createprofiledonor",
    },
    {
      label: "Logout",
      icon: logoutIcon,
      navigateRoute: "../../",
    },
  ];
  const dataLoggedIn: DataType[] = [
    {
      label: "Donor profile",
      icon: userIcon,
      navigateRoute: "../profile",
    },

    {
      label: "Logout",
      icon: logoutIcon,
      navigateRoute: "../../",
    },
  ];
  const [menuData, setMenuData] = useState<DataType[]>(dataNotLoggedIn);

  useEffect(() => {
    if (state.userInfo.is_account_created) {
      setMenuData(dataLoggedIn);
    }
  }, [state.userInfo.is_account_created]);

  useEffect(() => {
    if (state.userInfo.user_profile !== null) {
      setMenuData([...dataProfileCreated, ...dataLoggedIn]);
    }
  }, [state.userInfo.user_profile]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <IconButton
        ref={anchorRef}
        size="large"
        color={open ? "primary" : "default"}
        onClick={handleOpen}
        sx={{
          ...(open && {
            bgcolor: (theme) =>
              alpha(
                theme.palette.primary.main,
                theme.palette.action.focusOpacity
              ),
          }),
        }}
      >
        <Avatar sx={{ height: 30, width: 30 }} src={avatarProfile} />
      </IconButton>

      <MenuPopover
        open={open}
        onClose={handleClose}
        anchorEl={anchorRef.current}
        sx={{ width: 360 }}
      >
        <Scrollbar sx={{ height: { xs: 340, sm: "auto" } }}>
          <List>
            {menuData.map((item: DataType) => (
              <RenderItem
                key={item.label}
                label={item.label}
                icon={item.icon}
                navigateRoute={item.navigateRoute}
                handleClose={handleClose}
              />
            ))}
          </List>
        </Scrollbar>
      </MenuPopover>
    </>
  );
}
