import { useRef, useState } from "react";

import { useNavigate } from "react-router";

// material
import {
  List,
  Button,
  Typography,
  ListItemText,
  ListItemAvatar,
  ListItemButton,
} from "@material-ui/core";

import userIcon from "../assets/iconmenu/profile.png";

import MenuPopover from "../components/MenuPopover";
import Scrollbar from "../components/Scrollbar";
import { RootAppState } from "../utils/typeHelpers";
import { useSelector } from "react-redux";

// utils

// components

// ----------------------------------------------------------------------
interface DataType {
  label: string;
  icon: string;
  navigateRoute: string;
  handleClose?: () => void;
}
const data: DataType[] = [
  {
    label: "Create NGO Profile",
    icon: userIcon,
    navigateRoute: "../createprofilengo",
  },

  {
    label: "Create Donee Profile",
    icon: userIcon,
    navigateRoute: "../doneeinvitationcode",
  },
];

function RenderItem({ label, icon, navigateRoute, handleClose }: DataType) {
  const navigate = useNavigate();
  return (
    <ListItemButton
      disableGutters
      sx={{
        py: 1,
        px: 4,
        mt: "1px",
      }}
      onClick={() => {
        if (handleClose !== undefined) {
          handleClose();
        }
        navigate(navigateRoute);
      }}
    >
      <ListItemAvatar>
        <img
          style={{ height: 15, width: 15, objectFit: "contain" }}
          src={icon}
        />
      </ListItemAvatar>
      <ListItemText
        primary={
          <Typography
            variant="caption"
            sx={{
              display: "flex",
              alignItems: "center",
              color: "text.disabled",
            }}
          >
            {label}
          </Typography>
        }
      />
    </ListItemButton>
  );
}

export default function CreateProfileMenu() {
  const anchorRef = useRef(null);

  const [open, setOpen] = useState(false);
  const state = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.is_account_created
  );

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      {state && (
        <Button ref={anchorRef} color="inherit" onClick={handleOpen}>
          Start Fund Raising
        </Button>
      )}

      <MenuPopover
        open={open}
        onClose={handleClose}
        anchorEl={anchorRef.current}
        sx={{ width: 360 }}
      >
        <Scrollbar sx={{ height: { xs: 340, sm: "auto" } }}>
          <List>
            {data.slice(0, 2).map((item: DataType) => (
              <RenderItem
                key={item.label}
                label={item.label}
                icon={item.icon}
                navigateRoute={item.navigateRoute}
                handleClose={handleClose}
              />
            ))}
          </List>
        </Scrollbar>
      </MenuPopover>
    </>
  );
}
