import { useSelector } from "react-redux";
import { Outlet } from "react-router-dom";

//
import NavBarNotLoggedIn from "./navs/NavBarNotLoggedIn";
import { RootAppState } from "../utils/typeHelpers";
import NavbarLoggedIn from "./navs/LandingNavLoggedIn";

export default function AppLayoutwithNavbar() {
  const state = useSelector((state: RootAppState) => state.AuthReducer);
  return (
    <div>
      {state.isLoggedIn ? <NavbarLoggedIn /> : <NavBarNotLoggedIn />}
      <Outlet />
    </div>
  );
}
