import { noCase } from "change-case";
import { useRef, useState, useEffect, useCallback } from "react";
import { Link as RouterLink } from "react-router-dom";

import { Icon } from "@iconify/react";
import bellFill from "@iconify/icons-eva/bell-fill";
import clockFill from "@iconify/icons-eva/clock-fill";
import seenIcon from "@iconify/icons-eva/eye-fill";
import unseenIcon from "@iconify/icons-ant-design/eye-invisible";
// material
import { alpha } from "@material-ui/core/styles";
import {
  Box,
  List,
  Badge,
  Button,
  Avatar,
  Tooltip,
  Divider,
  IconButton,
  Typography,
  ListItemText,
  ListSubheader,
  ListItemAvatar,
  ListItemButton,
} from "@material-ui/core";
// utils

// components
import Scrollbar from "../../../../../components/Scrollbar";
import MenuPopover from "../../../../../components/MenuPopover";
import { NOTIFICATIONS_URL } from "../../../../../utils/Api";
import {
  NotificationApiResponse,
  NotificationObject,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import { useSelector } from "react-redux";
import useAxios from "../../../../../utils/useAxios";
import { useNavigate } from "react-router";
import { formatDistanceToNow } from "date-fns";

// ----------------------------------------------------------------------

function NotificationItem({
  notification,
  handleRefetch,
  handleClose,
}: {
  notification: NotificationObject;
  handleRefetch: () => void;
  handleClose: () => void;
}) {
  const {
    id,
    text,
    type,
    identifier,
    from_user,
    to_user,
    created_at,
    is_read,
  } = notification;
  const axiosInstance = useAxios();
  const navigate = useNavigate();
  const handleClick = async () => {
    await axiosInstance
      .put(`/live-notification-read-update/${id}/`, {
        is_read: true,
      })
      .then((e) => {
        handleClose();

        switch (type) {
          case "PROFILE_FOLLOW":
            navigate("../profiledonorother", {
              state: { goalID: identifier },
            });
            break;
          case "DONEE_INVITATION_ACCEPT":
            navigate("../profiledetailother", {
              state: { goalID: parseInt(identifier) },
            });
            break;
          case "GOAL_LIKE":
            navigate(`../viewgoal/${identifier}`);
            break;
          case "DONATION":
            navigate(`../viewgoal/${identifier}`);
            break;
          default:
        }
        if (e.status === 200) {
          handleRefetch();
        }
      });
  };

  return (
    <ListItemButton
      onClick={handleClick}
      disableGutters
      key={`${id}`}
      sx={{
        py: 1.5,
        px: 2.5,
        mt: "1px",
        backgroundColor: is_read === false ? "#fcfcfc" : "white",
      }}
    >
      <ListItemAvatar>
        <Avatar
          sx={{ bgcolor: "background.neutral" }}
          src={from_user.image}
        ></Avatar>
      </ListItemAvatar>
      <ListItemText
        primary={text}
        secondary={
          <Typography
            variant="caption"
            sx={{
              mt: 0.5,
              display: "flex",
              alignItems: "center",
              color: "text.disabled",
            }}
          >
            <Box
              component={Icon}
              icon={clockFill}
              sx={{ mr: 0.5, width: 16, height: 16 }}
            />
            {formatDistanceToNow(new Date(created_at))}
          </Typography>
        }
      />
      <Icon icon={is_read ? seenIcon : unseenIcon} />
    </ListItemButton>
  );
}

export default function NotificationsPopover({
  iconColor,
}: {
  iconColor: string;
}) {
  const axiosInstance = useAxios();
  const anchorRef = useRef(null);
  const ws = useRef<WebSocket | null>(null);
  const [open, setOpen] = useState(false);
  const [refetch, setRefetch] = useState<boolean>(true);
  const [sliceCount, setSlicedCount] = useState<number>(0);
  const [data, setData] = useState<NotificationApiResponse>();

  const userId = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.user_id
  );
  const getData = async () => {
    const response = await axiosInstance.get(`/live-notification-list`);
    setData(response.data);
  };
  useEffect(() => {
    getData();
  }, [refetch]);

  const handleRefetch = () => setRefetch(!refetch);

  useEffect(() => {
    ws.current = new WebSocket(NOTIFICATIONS_URL);
    ws.current.onopen = () => {};
    ws.current.onclose = () => {};
    ws.current.onmessage = (e) => {
      const parsedData = JSON.parse(e.data);
      if (parsedData.to_user === userId) {
        handleRefetch();
      }
    };
    const wsCurrent = ws.current;
    return () => {
      wsCurrent.close();
    };
  }, [refetch]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <IconButton
        ref={anchorRef}
        size="large"
        color={open ? "primary" : "default"}
        onClick={handleOpen}
        sx={{
          color: iconColor,
          ...(open && {
            bgcolor: (theme) =>
              alpha(
                theme.palette.primary.main,
                theme.palette.action.focusOpacity
              ),
          }),
        }}
      >
        <Badge badgeContent={data ? data.unread_count : 0} color="error">
          <Icon icon={bellFill} width={20} height={20} />
        </Badge>
      </IconButton>

      <MenuPopover
        open={open}
        onClose={handleClose}
        anchorEl={anchorRef.current}
        sx={{ width: 360 }}
      >
        <Box sx={{ display: "flex", alignItems: "center", py: 2, px: 2.5 }}>
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant="subtitle1">Notifications</Typography>
          </Box>

          {/* {totalUnRead > 0 && (
            <Tooltip title=" Mark all as read">
              <IconButton color="primary" onClick={handleMarkAllAsRead}>
                <Icon icon={doneAllFill} width={20} height={20} />
              </IconButton>
            </Tooltip>
          )} */}
        </Box>

        <Divider />

        <Scrollbar sx={{ maxHeight: { xs: 340, sm: "auto" } }}>
          <List
            disablePadding
            subheader={
              <ListSubheader
                disableSticky
                sx={{ py: 1, px: 2.5, typography: "overline" }}
              >
                New
              </ListSubheader>
            }
          >
            {data && data.list.length > 20
              ? data.list
                  .slice(0, 21)
                  .map((notification: NotificationObject) => (
                    <NotificationItem
                      key={notification.id}
                      notification={notification}
                      handleRefetch={handleRefetch}
                      handleClose={handleClose}
                    />
                  ))
              : data &&
                data.list.map((notification: NotificationObject) => (
                  <NotificationItem
                    key={notification.id}
                    notification={notification}
                    handleRefetch={handleRefetch}
                    handleClose={handleClose}
                  />
                ))}
          </List>
        </Scrollbar>

        <Divider />

        <Box sx={{ p: 1 }}>
          <Button fullWidth disableRipple component={RouterLink} to="#">
            View All
          </Button>
        </Box>
      </MenuPopover>
    </>
  );
}
